import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import getters from './getters'
import actions from './actions'
// vuex
Vue.use(Vuex)

// vue
let state = {
  // 
  dialog: false,
  // 
  sidebar: {
    open: false,
    docked: true
  },
  // 
  personindex: false,
  // 
  search: false,
  // 
  headerTitle: '',
  // 
  headerHref: 'message',
  // 
  data: { self: {}, friends: [] },
  // ajax
  isAjax: false,
  // friend_id
  activeId: 0,
  // 
  // _idlistlist{_id:xx, message:xxx},_id
  // messageai
  messageList: [
    {
      _id: 1,
      list: [{ _id: 1, message: '', time: '4:28' }]
    }, {
      _id: 2,
      list: [{ _id: 2, message: '', time: '9:50' }]
    }, {
      _id: 3,
      list: [{ _id: 3, message: '', time: '3:12' }]
    }
  ],
  // 
  messageListFB: [
    {
      _id: 1,
      list: [{ _id: 1, message: '', time: '4:28' }]
    }, {
      _id: 2,
      list: [{ _id: 2, message: '', time: '9:50' }]
    }, {
      _id: 3,
      list: [{ _id: 3, message: '', time: '3:12' }]
    }
  ]
}

// Store
export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
})



